<template>
  <div class="layout">
    <div class="header">
      <img src="./../../static/img/logo.png">
      <div class="logo" @click="alert('hhh');">
        
      </div>
      
      <div class="nav">
        <mu-tabs :value="activeTab" @change="handleTabChange" class="tab">
          <mu-tab value="1" title=""/>
          <mu-tab value="2" title=""/>
          <mu-tab value="3" title=""/>
          <mu-tab value="4" title=""/>
        </mu-tabs>
      </div>
    </div>
    <div class="content">
      <div class="body">
        <div class="contantGrid">
          <mu-row gutter>
            <mu-col width="50" tablet="30" desktop="33" class="columnShow">
              <mu-card>
                <mu-card-media title="">
                  <img src="./../../static/img/intelligence.jpg" class="animated pulse"/>
                </mu-card-media>
                <mu-card-title title=""/>
                <mu-card-text>
                  siri
                </mu-card-text>
                <!-- <mu-card-actions>
                  <mu-flat-button label="Action 1"/>
                </mu-card-actions> -->
              </mu-card>
            </mu-col>
            <mu-col width="50" tablet="30" desktop="33" class="columnShow">
              <mu-card>
                <mu-card-media title="">
                  <img src="./../../static/img/shortVideo.jpg" />
                </mu-card-media>
                <mu-card-title title=""/>
                <mu-card-text>
                  APPappapp
                </mu-card-text>
                <!-- <mu-card-actions>
                  <mu-flat-button label="Action 1"/>
                </mu-card-actions> -->
              </mu-card>
            </mu-col>
            <mu-col width="50" tablet="20" desktop="33" class="columnShow">
              <mu-card>
                <mu-card-media title="">
                  <img src="./../../static/img/deerDes.jpg" />
                </mu-card-media>
                <mu-card-title title=""/>
                <mu-card-text>
                  APPappapp
                </mu-card-text>
                <!-- <mu-card-actions>
                  <mu-flat-button label="Action 1"/>
                </mu-card-actions> -->
              </mu-card>
            </mu-col>
          </mu-row>

        </div>
      </div>
    </div>
    <div class="footer">
       2018 Created
    </div>

  </div>
</template>
<script>
export default {
  data () {
    return {
      activeTab: '1'
    }
  },
  methods: {
    handleTabChange (val) {
      this.activeTab = val
    }
  }
}
</script>
<style scoped>
.layout{
  background-color: rgb(236, 236, 236);
}

.header{
  background-color: #009688;
  margin-bottom: 40px;
  height: 80px;
}

.logo{
  font-size: 24px;
  color: white;
  display: inline-block;
  padding: 10px 20px;
}

.nav{
  display: inline-block;
  width: calc(100% - 150px);
  margin: 0 auto;
}

.tab{
  margin: 0 auto;
  width: 400px;
  background-color: rgba(0, 0, 0, 0);
}

.content{
  width: 90%;
  margin: 0 auto;
}

.breadcrumb{
  margin: 10px 0;
}

.body{
  background-color: white;
  border-radius: 5px;
  min-height: 650px;
}
.footer{
  padding: 20px 0;
  text-align: center;
}
.logo{
  line-height: 60px;
  position: relative;
  left: 90px;
}
.header img{
  width: 50px;
  position: relative;
  top: 16px;
  left: 98px;
}
.contantGrid{
  padding: 40px;
}
.columnShow{
  height: 600px;
}
</style>


