<template>
<div>
  <mu-text-field label="" hintText="" type="password" labelFloat v-model="oldPassword"/><br/>
  <mu-text-field label="" hintText="" type="password" labelFloat v-model="newPassworld"/><br/>
  <mu-text-field label="" hintText="" type="password" labelFloat v-model="aNewPassworld"/><br/>
  <mu-raised-button label="" class="demo-raised-button" style="margin-top: 50px;" @click="submitInfo()" primary/>
  <mu-popup position="top" :overlay="false" popupClass="demo-popup-top" :open="topPopup">
    
  </mu-popup>
</div>
</template>

<script>
export default {
  data () {
    return {
      oldPassword: '',
      newPassworld: '',
      aNewPassworld: '',
      topPopup: false
    }
  },
  methods: {
    submitInfo(){
      if(this.newPassworld != this.aNewPassworld){
        //
        this.topPopup = true
        setTimeout(() => {
          this.topPopup = false
        }, 2000)
      }else{
        //
        this.$ajax({
          method: 'post',
          url: 'http://47.94.254.3:3000/api/modifyPw',
          data: {
            oldPw: this.oldPassword,
            newPw: this.newPassworld
          }
        })
        .then((response) => {
          response = response.data.tableData
          //console.log(response)
          if(response == 1){
            alert('')
          }else{
            alert('')
          }
        })
      }
    }
  }
}
</script>
<style>
  .demo-popup-top {
    width: 100%;
    opacity: .9;
    height: 48px;
    line-height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 375px;
  }
</style>


